aside, main {
  padding: 10px; }

ul li {
  list-style-type: none; }
  ul li a {
    color: #fff; }

.modal-dialog {
  height: 600px !important; }
  .modal-dialog .modal-content .modal-body {
    padding: 60px 0px 0px 0px !important; }

.lines {
  top: -25px !important; }

.option-6 {
  width: 100%;
  height: 100%; }
  .option-6 .cs-header .cs-header-text {
    color: #000;
    font-size: 28px;
    padding: 125px 0px 0px 88px; }
  .option-6 .main-message {
    margin-top: 30px; }
  .option-6 .col-wrap {
    width: 100%;
    height: 100%;
    padding: 0px 0px; }
  .option-6 .col-50 {
    width: 50%;
    float: left;
    height: 500px;
    padding: 0px 40px; }
  .option-6 .action-button {
    background-color: #25408f;
    color: #fff;
    padding: 10px 28px;
    font-size: 16px;
    border-radius: 7px;
    margin-top: 20px;
    margin-left: 15px;
    position: absolute;
    bottom: 30px;
    right: 50px;
    display: inline-block; }

.code-box {
  margin: 20px 0px 40px 0px;
  padding-left: 35px; }

pre {
  white-space: normal; }

form {
  margin: 20px 0px 20px 0px; }

label {
  font-size: 14px; }



